:root {
	font-family: Georgia, serif;
	font-size: 10px;
	font-weight: 400;
	letter-spacing: 0.01rem;
	box-sizing: border-box;
	line-height: 1.6;
	margin: 0;
	padding: 0;
	color: rgba(0, 0, 0, 0.8);
}

:focus-visible {
	outline-color: #f83600;
	outline-offset: 5px;
}

::selection {
	background-color: #fe8c00;
}

body {
	margin: 0;
}

.page-container {
	margin: 0 auto;
	box-sizing: border-box;
	position: relative;
	width: 90%;
	max-width: 112rem;
	margin: 0 auto;
	padding: 0 2rem;
	@media screen and (max-width: 840px) {
		width: 100%;
	}
	@media screen and (max-width: 600px) {
		margin: 0;
	}
}

h1 a,
h2 a {
	color: black;
}
a {
	text-decoration: none;
	color: rgba(0, 0, 0, 0.5);
	transition: color 0.5s;
}
p {
	font-size: 1.6rem;
	line-height: 1.6;
	margin-top: 0;
	margin-bottom: 2.5rem;
}
ul,
ol {
	margin-bottom: 2.5rem;
	font-size: 1.6rem;
}
h2 {
	margin: 0 0 2rem 0;
	font-size: 3.6rem;
	letter-spacing: -0.1rem;
	line-height: 1.25;
	color: #000;
	font-family: 'Arial Black', sans-serif;
}
a:hover {
	cursor: pointer;
	color: #000;
}
h1,
h2,
h3 {
	margin: 0 0 2rem 0;
	color: #000;
	font-family: 'Arial Black', sans-serif;
}
h4 {
	font-size: 1.8rem;
}
h5 {
	font-size: 1.6rem;
}

