.article-title--full {
	font-size: 5rem;
	font-family: 'Arial Black', sans-serif;
	line-height: 1.2;
}
.article-title--preview {
	font-size: 4.4rem;
	color: black;
}
@media screen and (max-width: 600px) {
	.article-title--full {
		font-size: 4rem;
	}
	.article-title--preview {
		font-size: 3.6rem;
	}
}
@media screen and (max-width: 840px) {
	.article-title--preview {
		font-size: 3.2rem;
	}
}

.site-title,
.site-title:visited {
	font-family: 'Arial Black', sans-serif;
	display: inline-block;
	font-size: 2.2rem;
	color: #000;
}
.site-title:hover,
.article-title--preview a:hover,
.gradient-hover:hover {
	color: #fe8c00;
	background: -webkit-linear-gradient(left, #fe8c00, #f83600);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}
header {
	margin: 6rem 0 0 0;
}
@media screen and (max-width: 600px) {
	header {
		margin: 1rem 0 0 0;
	}
}
header .header-row {
	display: flex;
}
header .header-row-1 {
	margin-bottom: 10rem;
}
header .header-column-1 {
	flex: 0 0 65%;
	max-width: 65%;
}
@media screen and (max-width: 600px) {
	header .header-column-1 {
		margin: 0 auto;
	}
}
header .header-column-2 {
	flex: 0 0 35%;
	max-width: 35%;
}
header nav {
	list-style: none;
}
header nav ul {
	padding: 0;
	margin-top: 0;
}
header nav li {
	display: inline-block;
	font-size: 1.5rem;
}
header nav li a.active {
	color: rgba(0, 0, 0, 0.8);
	text-decoration: underline;
	text-decoration-color: lightgrey;
}
header nav li:not(:last-child) {
	margin-right: 6rem;
}

.search-form {
	width: 30rem;
}
.search-icon {
	right: 0;
	margin-left: 0;
	margin-right: 1.6rem;
	line-height: 3.8rem;
	position: absolute;
	vertical-align: middle;
}
.search-input {
	display: inline-block;
	outline: 0;
	height: 4rem;
	padding-left: 1rem;
	padding-right: 3.4rem;
	border: none;
	border-bottom: 2px solid #000;
	color: #000;
	background-color: transparent;
	padding: 0.6rem 1rem;
	font-size: 1.3rem;
	margin-bottom: 1.5rem;
	white-space: nowrap;
	float: right;
	transition: width 0.55s ease;
	width: 10rem;
}
.search-input:hover,
.search-input:focus {
	width: 30rem;
}

main {
	margin-top: 10rem;
}
.main--article {
	display: flex;
}
.main--article .col {
	margin-top: 0;
	width: 65%;
	flex: 65;
	padding: 0 1rem;
}
@media screen and (max-width: 600px) {
	.main--article {
		flex-direction: column;
	}
	.main--article .col {
		width: 100%;
		padding: 0;
		// text-align: justify;
	}
}
.article--full {
	margin-bottom: 10rem;
}
.article-header {
	margin-bottom: 10rem;
}
.article-date {
	font-style: italic;
}
@media screen and (max-width: 600px) {
	.article-date,
	.article-writer {
		font-size: 1.3rem;
	}
}

.sidebar {
	margin-left: 5%;
	flex: 35;
	width: 35%;
	padding: 0 1rem;
}
@media screen and (max-width: 600px) {
	.sidebar {
		width: 100%;
		padding: 0;
		margin: 0;
	}
}
.tags-section h3,
.recent-section h3 {
	font-size: 2rem;
}
.tags-section li,
.recent-section li {
	padding-bottom: 1rem;
	border-bottom: 1px dotted rgba(0, 0, 0, 0.2);
	list-style: none;
	font-size: 1.6rem;
}
@media screen and (max-width: 600px) {
	.tags-section li,
	.recent-section li {
		padding: 0.5rem 0 3rem 0;
	}
}
.tags-section ul,
.recent-section ul {
	padding-left: 0;
}
.tags-section {
	margin-bottom: 4rem;
}
@media screen and (max-width: 600px) {
	.tags-section {
		margin-bottom: 10rem;
	}
}

.article-info-container {
	flex: 0 0 40%;
	max-width: 40%;
}
.article-preview-container {
	display: flex;
	margin-bottom: 10rem;
}
/* .fadeIn {
  -webkit-animation-name: fadeIn;
  animation-name: fadeIn
} */
@media screen and (max-width: 600px) {
	.article-preview-container {
		flex-direction: column;
	}
}
.article-preview {
	font-size: 1.6rem;
	margin: 0 0 2.5rem 0;
	text-align: justify;
	color: #000;
}
.article-meta-col {
	flex: 0 0 40%;
	max-width: 40%;
}
@media screen and (max-width: 600px) {
	.article-meta-col {
		max-width: none;
	}
}
.article-content-col {
	margin-left: 5%;
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
}
@media screen and (max-width: 600px) {
	.article-content-col {
		margin-left: 0;
	}
}
.article-content-col p {
	text-align: justify;
}

.pagination {
	margin-bottom: 8rem;
	display: flex;
	justify-content: space-between;
}
.pagination ul {
	list-style: none;
}
.pagination ul li {
	display: inline-block;
}
.pagination ul li:nth-of-type(2) {
	margin-left: 3rem;
}
@media screen and (max-width: 600px) {
	.pagination {
		flex-direction: column;
	}
	.page-count {
		margin-bottom: 0;
	}
	.pagination ul {
		margin-top: 0;
		display: flex;
		justify-content: space-between;
		padding: 0;
	}
}

footer {
	border-top: 1px solid rgba(0, 0, 0, 0.5);
	padding: 4rem 0;
	display: flex;
}
.footer-col-1 {
	width: 65%;
}
.footer-col-2 {
	width: 35%;
	text-align: end;
}
.social-icons-list {
	margin-top: 0;
	list-style-type: none;
	padding: 0;
	li {
		display: inline-block;
		margin-right: 1rem;
		padding: 0.1em 1em;
		font-size: 1.6rem;
		background-color: #eee;
		border-radius: 3px;
	}
}
.copyright-notice {
	font-size: 1.3rem;
	color: rgba(0, 0, 0, 0.5);
}
.tag {
	margin-right: 0.8rem;
	padding: 0.2rem 1rem;
	font-size: 1.3rem;
	background-color: lightgrey;
	border-radius: 4px;
}

.top-of-page {
	font-size: 1.6rem;
	color: rgba(0, 0, 0, 0.5);
	transition: color 0.5s;
	border: none;
	background-color: transparent;
	font-family: Georgia, serif;
	padding: 0;
	&:hover {
		color: black;
	}
}

.pagination-next,
.pagination-previous {
	font-size: 1.6rem;
	color: black;
	&:hover {
		color: rgba(0, 0, 0, 0.5);
	}
}
.pagination-next::after {
	content: '→';
	position: relative;
	right: -0.5rem;
	transition: all 0.5s;
}
.pagination-next:hover::after {
	right: -1rem;
}
.pagination-previous::before {
	content: '←';
	position: relative;
	left: -0.5rem;
	transition: all 0.5s;
}
.pagination-previous:hover::before {
	left: -1rem;
}
.nav--error-page {
	ul {
		list-style: none;
		padding: 0;
	}
	li {
		margin-right: 2rem;
		display: inline-block;
	}
}

.next-prev-section {
	.row {
		display: flex;
		flex-direction: row;
		margin-bottom: 10rem;
		.column {
			padding: 0 1rem;
			width: 50%;
		}
		.next-prev-section .next {
			text-align: right;
		}
	}
}

@media screen and (max-width: 600px) {
	.next-prev-section {
		.row {
			flex-direction: column;
			.col {
				padding: 0;
				width: 100%;
			}
			.col-prev {
				margin-bottom: 4rem;
			}
		}
		.next {
			text-align: left;
		}
	}
}

.next-prev-section p {
	font-style: italic;
	margin-bottom: 1rem;
}
.next-prev-section h3 a {
	font-size: 2.6rem;
	color: black;
	line-height: 1.3;
}

.filtered-page-term {
	background-color: orange;
	background: -webkit-linear-gradient(left, #fe8c00, #f83600);
	padding: 0.2rem 0.8rem;
	border-radius: 0.2rem;
}
@media screen and (max-width: 600px) {
	.filter-term-heading {
		line-height: 1.5;
	}
}

.markdown-content {
	a {
		text-decoration: underline;
	}
	ul,
	ol {
		list-style-position: inside;
		padding: 0;
	}
	pre {
		width: 80%;
		background-color: rgb(40, 42, 54);
		.code-block-header {
			display: flex;
			justify-content: space-between;
			padding: 10px;
			button {
				height: min-content;
			}
			.language {
				color: whitesmoke;
			}
		}
	}
	h3 {
		font-size: 1.6rem;
		margin-bottom: 1rem;
	}
	@media screen and (max-width: 600px) {
		pre {
			width: 100%;
		}
		p {
			text-align: justify;
		}
	}
	code {
		font-size: 1.6rem;
		background-color: rgba(255, 165, 0, 0.5);
		padding: 0.2rem 0.8rem;
		border-radius: 3px;
	}
}
